import { Component, Input, OnInit, ChangeDetectionStrategy } from '@angular/core';

/**
 * @name EmptyStateComponent
 *
 * @description
 * Component that displays an empty state.
 */
@Component({
  selector: 'bb-empty-state-ui',
  templateUrl: './empty-state.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class EmptyStateComponent implements OnInit {
  /**
   * The title of the empty state.
   * Will throw an error if unset.
   */
  @Input() title!: string | undefined;
  /**
   * The sub-title of the empty state. Defaults to an empty string
   */
  @Input() subtitle: string | undefined;
  /**
   * Whether the icon should be shown. Defaults to true
   */
  @Input() showIcon = true;
  /**
   * The icon classes for the empty state. Defaults to an empty string
   */
  @Input() iconClasses: string | undefined;
  /**
   * The modifier for the icon used in the component. Defaults to 'list'
   */
  @Input() iconModifier = 'list';
  /**
   * The size for the icon used in the component. Defaults to 'xl'
   */
  @Input() iconSize = 'xl';
  /**
   * The color for the icon used in the component. Defaults to an empty string
   */
  @Input() iconColor: string | undefined;

  ngOnInit() {
    if (typeof this.title === 'undefined') {
      throw new Error(`"title" input is required in "${this.constructor.name}"`);
    }
  }
}
